@use 'helpers/variables' as v;
@use 'helpers/mixins' as m;

.videojuego {
    width: 99%;
    max-width: 160rem;
    margin: 0 auto;
    margin-top: 4rem;
    margin-bottom: 4rem;
    @include m.escritorio {
        margin-top: 8rem;
        margin-bottom: 8rem;
    }
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 2rem;
    @include m.escritorio { 
        grid-template-columns: 1fr 2fr; 
        align-items: flex-start;
    }
}

.videojuego_contenido {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
    .videojuego-titulo {
        margin: 0;
        color: v.$blanco;
    }
    .videojuego-precio {
        font-size: 2.5rem;
        color: v.$verde;
        font-weight: v.$bolder;
    }
    .videojuego-plataforma img {
        width: 5rem;
        height: auto;
    }
    .videojuego-plataforma_texto {
        font-size: 2.5rem;
        font-weight: v.$bolder;
        color: v.$blanco;
    }
    .videojuego-descripcion {
        font-size: 1.8rem;
        font-family: v.$font;
        // text-align: justify;
        line-height: normal;
        letter-spacing: 1px;
        color: v.$blanco;
        white-space: pre-wrap;
    }
    img {
        width: 2rem;
        filter: invert(100%);
    }
    button {
        font-size: 2rem;
        background-color: v.$moradoClaro;
        border: 0;
        padding: 1rem;
        border-radius: 2%;
        color: v.$blanco;
        cursor: pointer;
        &:hover {
            background-color: darken(v.$moradoClaro, 5);
        }
        width: max-content;
    }
}